.backgroundimg {
	position: fixed; 

	/*s positioned relative to the viewport, which means it always stays 
	in the same place even if the page is scrolled. The top, right, bottom, and left 
	properties are used to position the element.*/
	
	width: 100%;
	height: 100%;
	top: 50;
    z-index: -1;
    background: 
    linear-gradient(
      rgba(96, 78, 112, .95),
      rgba(96, 78, 112, .95)
    ),
    url(/portfolio/static/media/bartosz-kwitkowski-509565-unsplash.180e8e98.jpg);
    background-repeat: no-repeat;
    background-size: 350%;
 
}

@media screen and (min-width: 568px){
.backgroundimg {
	background-size: 220%;
}

}


@media screen and (min-width: 1200px){
.backgroundimg {
	background-size: 116%;
}

}


/*WebsiteOptionMenu*/

.websitebuttonbannero {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	width: 100%;
	height: 50px;
	z-index: 1;
	/*background-color: yellow;*/
}

.websitebuttongroupo {
	display: flex;
	flex-direction: row;
}

.websitebuttono {
	width: 100%;
	height: 50px;
	background:#1AAB8A;
	color:#fff;
	border: none;
	position: relative;
	cursor: pointer;
	transition:800ms ease all;
	outline: none;
}

.websitebuttono:hover{
  background:#fff;
  color:#1AAB8A;
}

.websitebuttono:before,.websitebuttono:after{
  content:'';
  position:absolute;
  top:0;
  right:0;
  height:3px;
  width:0;
  background: #1AAB8A;
  transition:400ms ease all;
}

.websitebuttono:after{
  right:inherit;
  top:inherit;
  left:0;
  bottom:0;
}

.websitebuttono:hover:before,.websitebuttono:hover:after{
  width:100%;
  transition:800ms ease all;
}
